.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fullpage {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  background-color: black;
}

.cropper {
  width: 45vh;
  height: 80vh;
  margin-top: 5vh !important;
  margin: auto;
  z-index: 4;
}

.cropper video {
  width: 45vh;
  height: 80vh;
  position: absolute;
  top: 5vh !important;
  left: calc(50vw - 22.5vh) !important;
  z-index: 0;
}

.interactBar {
  width: 80vw;
  height: 12.5vh;
  margin-top: 2.5vh !important;
  margin: auto;
  z-index: 4;
}

.interactBtn {
  width: fit-content;
  height: fit-content;
  margin: auto;
}